:root {
  --background: rgba(255, 255, 255, 1);
  --navbarBackground: rgb(53, 53, 53);
  --textNavbar: rgba(255, 255, 255, 1);
  --text: rgba(55, 55, 55, 1);
  --prognozaCardBackground: rgba(55, 55, 55, 1);
  --prognozaCardText: rgba(255, 255, 255, 1);
  --margine: 15%;
}
body {
  border: 0;
  font-size: 100%;
  box-sizing: border-box;
  font-family: arial;
  display: block;
  cursor: default;
}
a {
  text-decoration: none;
  cursor: pointer;
  color: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none; /* Chrome 49+ */
  -moz-user-select: none; /* Firefox 43+ */
  -ms-user-select: none; /* No support yet */
  user-select: none;
}
.App {
  overflow: hidden;
}
.Navbar {
  background-color: var(--navbarBackground);
  color: var(--textNavbar);
  width: 100%;
  padding: 1vh var(--margine) 1vh var(--margine);
  font-size: 4vh;
  display: flex;
  flex-direction: row;
}
.NavbarDreapta {
  display: flex;
  flex: 1;
  flex-direction: row-reverse;
}
.NavbarDreapta div {
  padding-left: 20px;
  cursor: pointer;
}
.Content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 1vh;
}
.PrognozaTop {
  margin: 0 var(--margine) 0 var(--margine);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 5vh;
}
.PrognozaCard {
  height: 160px;
  width: 120px;
  padding: 10px;
  background-color: var(--prognozaCardBackground);
  border-radius: 10px;
  box-shadow: 0px 4px 5px 2px gray;
  color: var(--prognozaCardText);
}
.PrognozaCardTemperaturi {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.Footer {
  margin: 0;
  padding: 3vh;
  width: 100%;
  font-size: 3vh;
  display: flex;
  justify-content: center;
  background-color: var(--navbarBackground);
  color: var(--textNavbar);
  margin-top: 10vh;
}
.marginTopp {
  margin-top: 10px;
}
.PrognozaCardLoading {
  height: 160px;
  width: 120px;
  padding: 10px;
  background-color: var(--prognozaCardBackground);
  border-radius: 10px;
  box-shadow: 0px 4px 5px 2px gray;
  color: var(--prognozaCardText);
  display: flex;
  justify-content: center;
  align-items: center;
}
.PrognozaCardLoading img {
  width: 30px;
  height: 30px;
}
.PrognozaAzi {
  margin: 0 var(--margine) 0 var(--margine);
}
.PrognozaAziContainer {
  background-color: var(--prognozaCardBackground);
  color: var(--textNavbar);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 2px 5px 1px gray;
  display: flex;
  width: 100%;
  min-height: 350px;
  flex-direction: row;
}
.PrognozaAziContainer img {
  width: 150px;
  height: 150px;
}
.PrognozaCurentLoading {
  height: 300px;
  width: 100%;
  background-color: var(--prognozaCardBackground);
  color: var(--textNavbar);
  border-radius: 10px;
  box-shadow: 0px 2px 5px 1px gray;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 350px;
}
.PrognozaCurentLoading img {
  width: 100px;
  height: 100px;
}
.flexcolumn {
  display: flex;
  flex-direction: column;
}
.flexrow {
  display: flex;
  flex-direction: row;
}
.titluPrognoza {
  margin: 10px 0 10px 30px;
  font-size: 7vh;
  font-weight: 900;
}
.PrognozaGrade {
  font-size: 130px;
}
.PrognozaZile {
  margin: 10px 0 5px 30px;
}

.PrognozaDreapta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 30px;
  padding-top: 10px;
  width: 100%;
  min-width: 0;
  overflow: hidden;
}
.fade {
  opacity: 0;
}
.fade-appear {
  transform: translate(-50px);
  opacity: 0;
}
.fade-appear-active {
  transform: translate(0, 0);
  opacity: 1;
  transition: 1000ms;
}
.fade-appear-done {
  opacity: 1;
  transition: opacity 1000ms;
}
.fade2 {
  opacity: 0;
}
.fade2-appear {
  transform: translate(50px, 0);
  opacity: 0;
}
.fade2-appear-active {
  transform: translate(0, 0);
  opacity: 1;
  transition: 1200ms;
}
.fade2-appear-done {
  opacity: 1;
  transition: opacity 1200ms;
}
.fade3-appear {
  transform: translate(0, -50px);
}
.fade3-appear-active {
  transform: translate(0, 0);
  transition: 500ms;
}
.chart {
  width: 100%;
  height: 100%;
  margin-top: 55px;
}
.chartMare {
  height: 350px;
  padding: 5vh 0 5vh 0;
  margin: 0 var(--margine) 0 var(--margine);
  background-color: var(--prognozaCardBackground);
  color: var(--textNavbar);
  border-radius: 10px;
  box-shadow: 0px 2px 5px 1px gray;
  display: flex;
  justify-content: center;
}
.Vizibil {
  width: 100%;
  min-height: 40px;
  margin-top: 5vh;
}
@media only screen and (max-width: 1250px) {
  :root {
    --margine: 5%;
  }
}
@media only screen and (max-width: 800px) {
  :root {
    --margine: 1%;
  }
  .PrognozaAziContainer {
    flex-direction: column;
    align-items: center;
  }
  .Navbar {
    padding: 1vh 4% 1vh 4%;
  }
  .PrognozaDreapta {
    align-items: center;
  }
  .flexcolumn {
    padding: 0;
    align-items: center;
  }
  .flexcolumn div {
    margin: 2vh 0 0 0;
  }
}
@media only screen and (max-width: 600px) {
  body {
  }
}
